"use client"

import { Button } from "@/components/ui/button"
import { Phone, HeartPulse, ShieldAlert, Ambulance } from "lucide-react"
import { useLanguage } from "@/contexts/LanguageContext"
import Link from "next/link"

export default function EmergencyService() {
  const { t } = useLanguage()

  const services = [
    {
      icon: <HeartPulse className="h-6 w-6 text-red-600 dark:text-red-300" />,
      title: t("emergency.services.medical.title"),
      description: t("emergency.services.medical.description"),
    },
    {
      icon: <ShieldAlert className="h-6 w-6 text-red-600 dark:text-red-300" />,
      title: t("emergency.services.accident.title"),
      description: t("emergency.services.accident.description"),
    },
    {
      icon: <Ambulance className="h-6 w-6 text-red-600 dark:text-red-300" />,
      title: t("emergency.services.firstAid.title"),
      description: t("emergency.services.firstAid.description"),
    },
  ]

  return (
    <section className="py-20 bg-gray-50 dark:bg-gray-900">
      <div className="container mx-auto px-4">
        <div className="text-center mb-16">
          <div className="inline-flex items-center px-4 py-2 bg-red-100 dark:bg-red-900 text-red-800 dark:text-red-200 rounded-full mb-4">
            <Phone className="h-4 w-4 mr-2 animate-pulse" />
            <span className="text-sm font-medium">{t("emergency.tag")}</span>
          </div>
          <h2 className="text-4xl font-extrabold text-gray-900 dark:text-white mb-4">
            {t("emergency.title")}
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300 max-w-2xl mx-auto">
            {t("emergency.description")}
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 mb-12">
          {services.map((service, index) => (
            <div
              key={index}
              className="bg-white dark:bg-gray-800 rounded-xl p-8 shadow-md transition-shadow hover:shadow-lg"
            >
              <div className="flex items-center justify-center w-16 h-16 bg-red-50 dark:bg-red-950 rounded-full mb-6">
                {service.icon}
              </div>
              <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-2">
                {service.title}
              </h3>
              <p className="text-gray-600 dark:text-gray-400">{service.description}</p>
            </div>
          ))}
        </div>

        <div className="text-center">
          <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
            {t("emergency.ctaTitle")}
          </h3>
          <p className="text-gray-600 dark:text-gray-300 mb-6">
            {t("emergency.ctaDescription")}
          </p>
          <div className="flex flex-col sm:flex-row justify-center gap-4">
            <Button className="bg-red-600 hover:bg-red-700 dark:bg-red-500 dark:hover:bg-red-600 text-white font-bold px-8 py-6 rounded-full text-lg shadow-lg hover:scale-105 transition-transform">
              <Link href="tel:+250788303030">
                {t("emergency.callButton")} <Phone className="ml-3 h-5 w-5" />
              </Link>
            </Button>
            <Button
              variant="outline"
              className="border-red-600 text-red-600 hover:bg-red-50 dark:border-red-400 dark:text-red-400 dark:hover:bg-gray-800 font-bold px-8 py-6 rounded-full text-lg shadow-lg hover:scale-105 transition-transform"
            >
              {t("emergency.getDirectionsButton")}
            </Button>
          </div>
        </div>
      </div>
    </section>
  )
}